.actions {
  display: none;
}

.card {
  bottom: 20%;
  display: flex;
  background: #5da979;
  margin: 0 auto;
  width: 80%;
  position: absolute;
  /* top: 50%; */
  left: 0;
  right: 0;
  padding: 20px;
}
input {
  font-size: 100%;
}

button,
input[type="submit"] {
  background: #eaeaea;
  border: none;
  padding: 10px;
  border-radius: 14px;
}
.edit-players-initials {
  display: flex;
  flex-direction: column;
}

.score-form {
  display: flex;
  flex-direction: column;
}

.score-card {
  display: flex;
  flex-direction: row;
  padding: 20px;
}

.score-col {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.score-input {
  flex: 1;
  height: 40px;
  width: 100px;
}
